/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

/* Mobile View/Tablet View Hidden*/
#Tablet_HomePageNavbarSection,
#Mobile_HomePageNavbarSection {
    display: none;
}


#Global_HomePageNavbarSection {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: var(--base2-color);
    box-shadow: var(--box-shadow);
    height: 80px;
}

.Global_HomePageNavbarSection1 {
    align-items: center;
    padding-left: 20px;
}

.Global_HomePageLink {
    text-decoration: none;
}

#Global_HomePageCompanyTitle {
    align-items: center;
    text-shadow: var(--box-shadow);
    font-size: 1.5rem;
    font-weight: bold;
    color: red;
}

.Global_HomePageNavbarSection2 {
    padding-right: 20px;
    display: flex;
    box-sizing: border-box;
}

.Global_HomePageNavbarSection2ListItem {
    padding-left: 5px;
    padding-right: 5px;
    list-style-type: none;
}

.Global_HomePageNavLink {
    font-size: 1rem;
    text-decoration: none;
}

.Global_HomePageNavLinkTitle {
    font-weight: bold;
    color: red;
}

.Global_getStartedItem {
    list-style-type: none;
    padding-left: 10px;
}

.Global_HomePageGetStarted {
    box-shadow: var(--box-shadow);
    background-color: red;
    border-radius: 8px;
}

.Global_HomePageGetStartedNavLink {
    color: white;
}

/*Tablet View*/
@media (max-width:768px) {

    /*
    Mobile View/Global View Hidden
    */
    #Global_HomePageNavbarSection,
    #Mobile_HomePageNavbarSection {
        display: none;
    }

    #Tablet_HomePageNavbarSection {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        background-color: var(--base2-color);
        box-shadow: var(--box-shadow);
        height: 80px;
    }

    .Tablet_HomePageNavbarSection1 {
        align-items: center;
        padding-left: 20px;
    }

    .Tablet_HomePageLink {
        text-decoration: none;
    }

    #Tablet_HomePageCompanyTitle {
        align-items: center;
        text-shadow: var(--box-shadow);
        font-size: 1.5rem;
        font-weight: bold;
        color: red;
    }

    .Tablet_HomePageNavbarSection2 {
        padding-right: 20px;
        display: flex;
        box-sizing: border-box;
    }

    .Tablet_HomePageNavbarSection2ListItem {
        padding-left: 5px;
        padding-right: 5px;
        list-style-type: none;
    }

    .Tablet_HomePageNavLink {
        font-size: 1rem;
        text-decoration: none;
    }

    .Tablet_HomePageNavLinkTitle {
        font-weight: bold;
        color: red;
    }

    .Tablet_getStartedItem {
        list-style-type: none;
        padding-left: 10px;
    }

    .Tablet_HomePageGetStarted {
        box-shadow: var(--box-shadow);
        background-color: red;
        border-radius: 8px;
    }

    .Tablet_HomePageGetStartedNavLink {
        color: white;
    }

}

/*/////////////////////////////////*/
/*Mobile View*/
@media (max-width:390px) {

    /* 
    Mobile View/Tablet View Hidden
    */
    #Global_HomePageNavbarSection,
    #Tablet_HomePageNavbarSection {
        display: none;
    }



    #Mobile_HomePageNavbarSection {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        background-color: var(--base2-color);
        box-shadow: var(--box-shadow);
        height: 8vh;
    }

    .Mobile_HomePageNavbarSection1 {
        padding-left: 20px;
    }

    .Mobile_HomePageLink {
        text-decoration: none;
        text-align: 0;
    }

    #Mobile_HomePageCompanyTitle {
        align-items: center;
        text-shadow: var(--box-shadow);
        font-size: 1.5rem;
        font-weight: bold;
        color: red;
    }

    .Mobile_HomePageNavbarSection2 {
        background-color: var(--primary-color);
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 45px;
        width: 100%;
        height: min(100vh);
        z-index: 1;
        background-color: lightyellow;
        padding-left: 0;
    }

    .Mobile_HomePageNavbarSection2ListItem,
    .Mobile_getStartedItem {
        list-style: none;
        box-shadow: var(--box-shadow);
    }

    .Mobile_HomePageNavLink {
        margin: 0;
        text-decoration: none;
        text-align: center;
    }

    .Mobile_HomePageLinkTitle {
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

    .Mobile_getStartedItem {
        display: flex;
        justify-content: center;
    }
}