/*CUSTOM COLORS*/
:root {
    /*--Colors--*/
    --base-color: #161618;
    --base2-color: #1C1C1E;
    --trim-color: #000000;
    --primary-color: #212124;
    --secondary-color: #818181;
    --test-color: rgb(2, 211, 248);
    --error-color: ;
    --pass-color: ;
    --clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    /*--Sizes--*/
    --full-height: 100vh;
    --full-width: 100vw;
    --box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

/* Mobile View/Tablet View Hidden*/
#Tablet_AboutPageMainSection,
#Mobile_AboutPageMainSection {
    display: none;
}

/* <!--REORDERED--> */

.SectionCardDisplayButton {
    cursor: pointer;
    background-color: blue;
    height: 20px;
}

.testZ {
    display: block;
    top: 20px;
    position: absolute;
    z-index: 2;
}

#Global_AboutPageMainSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
    color: #818181;
    justify-content: center;
    height: 750px;
}


.Global_AboutPageSectionTitle {
    align-items: center;
    text-align: 'center';
}

.Global_Introduction {
    padding: 0;
    height: 100%;
    margin: 0;
}

.Global_AboutPageSectionOptionsButtons {
    display: flex;
    flex-direction: row;
}

.Global_AboutPageSectionOption1Display {
    width: auto;
}

.Global_AboutPageSectionContainerCards {
    width: 100% auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 0;
}

.TestBlock {
    display: none;
}

/* <!--Section 1 Card--> */
.Global_AboutPageSection1Card {
    border-radius: 5px;
    padding-right: 5px;
    padding-left: 5px;
    background-color: red;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    height: 500px;
}

.Global_AboutPageSection1CardTitle {
    margin: 0;
    background-color: orange;
    border: 1px solid white;
}

.Global_AboutPageSection1CardItems {
    background-color: orange;
    border: 1px solid white;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
}


.Global_AboutPageSection1Image {
    background-color: yellow;
    margin: 0px auto;
    border: 1px solid white;
}

.Global_AboutPageSection1Content {
    background-color: yellow;
    margin: 0px auto;
    border: 1px solid white;
    width: 600px;
}

