#Tablet_AboutPageFooterSection,
#Mobile_AboutPageFooterSection {
    display: none;
}

#Global_AboutPageFooterSection {
    display: flex;
    flex-direction: column;
    color: yellow;
    box-shadow: var(--box-shadow);
}

.Global_AboutPageFooterCards {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
    display: flex;
    flex-direction: row;
}

.Global_AboutPageFooterCategory {
    display: flex;
    flex-direction: column;

    width: 250px;
    align-items: center;
    border: 1px solid black;
    box-shadow: var(--box-shadow);
    border-radius: 20px;
    height: 200px;
    padding: 0;
    margin: 10px;

}

.Global_AboutPageFooterCategoryName {
    margin: 0;
    padding: 0;
    text-decoration: none;
    padding: 0;
    color: purple;
    font-weight: bold;
    font-size: 1.2rem;
}

.Global_AboutPageFooterCategoryNameBar {
    margin: 0;
    padding: 0;
    width: 100%;
}

.Global_AboutPageFooterCategoryList {
    margin-top: 20px;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    padding: 0;
    padding-bottom: 10px;
}

.Global_AboutPageFooterCategoryItem {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    padding-bottom: 5px;
}

.Global_AboutPageFooterCategoryItemLink {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: red;
    font-weight: bold;
    font-size: 1rem;
}

.Global_AboutPageSubList {
    margin: 0;
    padding: 0;
    visibility: hidden;
}

.Global_github {
    margin: 0;
    padding: 0;
    width: 125px;

    border: 0;
    height: 32px;
    border-radius: 8px;
}

.Global_AboutPageFooterBox {
    margin: 0;
    padding: 0;
    display: block;
}

